import React from "react";
import { connect } from "react-redux";
import LogoQuote from "../../../svg/logoQuote";
import {FormattedMessage} from "react-intl";
import AdjustShadingButtons from "./adjustShadingButtons";
import {SHADING_BUTTONS} from "../../../reducers/adjustReducer";
import {setShadingType} from "../../../actions/adjustActions";

class AdjustShading extends React.Component {

  handleButtonClick = (shadingType) => {
    this.props.setShadingType(shadingType)
  };

  getButtonFormattedMessageId = (shadingType) => {
    switch (shadingType) {
      case SHADING_BUTTONS.sun:
        return "adjustShading.buttonUnshaded";
      case SHADING_BUTTONS.tree:
        return "adjustShading.buttonPartlyShady";
      case SHADING_BUTTONS.shady:
        return "adjustShading.buttonShaded";
      default:
        return ""
    }
  };

  render() {
    return (
      <div className="container">
        <LogoQuote style={{marginBottom: "60px"}}/>
        <p className="text-center" style={{margin: "0 0 30px 0"}}><FormattedMessage id="adjustShading.question"/></p>
        <div className="flex-container" style={{marginBottom: "60px"}}>
          {Object.keys(SHADING_BUTTONS).map(shadingType => (
            <button className="buttons-icon" key={shadingType} onClick={() => this.handleButtonClick(shadingType)}>
              <span className={"buttons-icon__img buttons-icon__img--" + shadingType
              + (shadingType === this.props.shadingType ? " buttons-icon__img--selected" : "")}/>
              <span className="buttons-icon__text">
                <FormattedMessage id={this.getButtonFormattedMessageId(shadingType)}/>
              </span>
            </button>
          ))}
        </div>
        <AdjustShadingButtons/>
      </div>
    )
  }
}

const mapStateToProps = (store) => {
  return {
    shadingType: store.adjustState.shadingType,
  };
};

export default connect(mapStateToProps, {
  setShadingType
})(AdjustShading);
