import React, {Fragment} from "react";
import { connect } from "react-redux";

import UiInput from "../ui/input";
import LogoQuote from "../../svg/logoQuote";
import {setUserInputValue} from "../../actions/loginFunctions";
import LoginButton from "./loginButton";
import LoadingSpinner from "../ui/loadingSpinner";

class Login extends React.Component {

  state = {
    submitted: false
  };

  handleButtonClick = () => {
    this.setState({
      submitted: true
    })
  };

  render() {
    return (
      <div className="container">
        <LogoQuote style={{marginBottom: "100px"}}/>
        {this.state.submitted ? (
          <Fragment>
            <div className="text-center" style={{marginBottom: "20px"}}><LoadingSpinner/></div>
            <p className="text-center">Please wait while we generate your quote…</p>
          </Fragment>
        ) : (
          <Fragment>
            <UiInput onChange={this.props.setUserInputValue}
                     property="fullName" placeholder={"login.inputPlaceholderFullName"} value={this.props.fullName}/>
            <UiInput onChange={this.props.setUserInputValue}
                     property="phone" placeholder={"login.inputPlaceholderPhone"} value={this.props.phone}/>
            <UiInput onChange={this.props.setUserInputValue}
                     property="email" placeholder={"login.inputPlaceholderEmail"} value={this.props.email}/>
            <UiInput onChange={this.props.setUserInputValue}
                     property="zip" placeholder={"login.inputPlaceholderZip"} value={this.props.zip}/>
            <LoginButton handleButtonClick={this.handleButtonClick}/>
          </Fragment>
        )}
      </div>
    )
  }
}

const mapStateToProps = (store) => {
  return {
    fullName: store.userState.fullName,
    phone: store.userState.phone,
    email: store.userState.email,
    zip: store.userState.zip,
  };
};

export default connect(mapStateToProps, {
  setUserInputValue
})(Login);
