import * as types from '../actions/actionTypes';

const initialState = {
  solarOutputCalculated: false,
  panelCostsCalculated: false,
  monthlyBillCalculated: false,
  everythingCalculated: false,
};

export default function (state = initialState, action) {

  switch (action.type) {

    case types.SET_SOLAR_OUTPUT_CALCULATED:
    case types.SET_PANEL_COSTS_CALCULATED:
    case types.SET_MOTHLY_BILL_CALCULATED:
    case types.SET_EVERYTHING_CALCULATED:
      return Object.assign({}, state, {
        ...action.payload
      });
    default:
      return state
  }

}
