import * as types from './actionTypes';

export const setUserInputLocation = (userInputLocation) => ({
  type: types.SET_USER_INPUT_LOCATION,
  payload: { userInputLocation }
});

export const setGoogleMapsLocation = (googleMapsLocation, googleMapsPlace) => {
  if (googleMapsPlace) {
    delete googleMapsPlace.utc_offset
  }
  return {
    type: types.SET_GOOGLE_MAPS_LOCATION,
    payload: { googleMapsLocation, googleMapsPlace }
  }
};

export const setGoogleMapsMarker = (markerPlaced) => ({
  type: types.SET_GOOGLE_MAPS_MARKER,
  payload: { markerPlaced }
});
