import React, {Fragment} from "react";
import {FormattedMessage} from "react-intl";
import LoadingSpinner from "../../ui/loadingSpinner";

export default function (props) {
  return (
    <div id="calculation-section">
      {props.panelCostsCalculated ? (
        <div id="section-wrapper">
          <div id="section-icon">
            {props.everythingCalculated ? <img src="/icons/icon-lock.svg" id="unlock-icon" alt={""} /> : null}
            <img src="/icons/icon-solar-panels.svg" id="icon-properties" alt={""}/>
          </div>
          <h1><FormattedMessage id="calculation.panelCosts"/></h1>
          <h2><FormattedMessage id="calculation.calculated"/></h2>
        </div>
      ) : (
        <Fragment>
          <LoadingSpinner/>
          <p><FormattedMessage id="calculation.calculatingSolarPanelCost"/></p>
        </Fragment>
      )}
      <div id="calculation-line-wrapper">
        <div id="calculation-section-line"/>
      </div>
    </div>
  )
}
