import React from "react";
import { connect } from "react-redux";
import ReactSlider from "react-slider";
import {FormattedMessage} from "react-intl";

import {setSolarPanelsAmount} from "../../../actions/solarFunctions";

class SolarPanelsSlider extends React.Component {

  state = {
    value: this.props.value,
    editing: false
  };

  componentDidMount() {
    this.setState({
      value: this.props.solarPanelsAmount
    })
  }

  onChange = (value) => {
    this.setState({
      value
    })
  };

  onAfterChange = (event) => {
    this.setState({
      editing: false
    }, () => {
      this.props.setSolarPanelsAmount(this.state.value);
    })
  };

  onBeforeChange = () => {
    this.setState({
      editing: true,
      value: this.props.solarPanelsAmount
    })
  };

  render() {
    return (
      <div className="solar-panel__slider">
        <ReactSlider
          min={this.props.solarPanelsAmountMin}
          max={this.props.solarPanelsAmountMax}
          onChange={this.onChange}
          onBeforeChange={this.onBeforeChange}
          onAfterChange={this.onAfterChange}
          value={this.state.editing ? this.state.value : this.props.solarPanelsAmount}
          className="slider-wrapper"
          thumbClassName="slider-button"
          trackClassName="slider-range"
          renderThumb={(props, state) => <div {...props}>{state.valueNow}</div>}
        />
        <p><FormattedMessage id="adjustSolar.solarSliderText" /></p>
      </div>
    )
  }
}

const mapStateToProps = (store) => {
  return {
    solarPanelsAmountMin: store.solarState.solarPanelsAmountMin,
    solarPanelsAmountMax: store.solarState.solarPanelsAmountMax,
    solarPanelsAmount: store.solarState.solarPanelsAmount,
  };
};

export default connect(mapStateToProps, {
  setSolarPanelsAmount
})(SolarPanelsSlider);
