import * as types from '../actions/actionTypes';

export const SHADING_BUTTONS = {
  sun: "sun",
  tree: "tree",
  shady: "shady"
};

const initialState = {
  electricBillMin: 0,
  electricBillMax: 20000,
  electricBillAmount: 10000,
  shadingType: null
};

export default function (state = initialState, action) {

  switch (action.type) {

    case types.SET_SHADING_TYPE:
    case types.SET_ELECTRIC_BILL_AMOUNT:
      return Object.assign({}, state, {
        ...action.payload
      });
    default:
      return state
  }

}
