import React from "react";
import PropTypes from 'prop-types';
import {FormattedMessage} from "react-intl";

export default class UiInput extends React.Component {

  inputElement = null;

  static propTypes = {
    value: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired,
    onChange: PropTypes.func.isRequired,
    property: PropTypes.string,
    placeholder: PropTypes.string,
    setRef: PropTypes.func,
  };

  state = {
    value: this.props.value,
    editing: false
  };

  onChange = (event) => {
    this.setState({
      value: event.target.value
    })
  };

  onBlur = (event) => {
    let value = event.target.value;
    this.finishEditing(value);
  };

  finishEditing = (value) => {
    this.setState({
      editing: false
    }, () => {
      this.props.onChange(value, this.props.property);
    })
  };

  onFocus = () => {
    this.setState({
      editing: true,
      value: this.props.value
    })
  };

  setRef = (node) => {
    this.inputElement = node
  };

  render() {
    return (
      <FormattedMessage id={this.props.placeholder || "placeholder-missing"} defaultMessage="">
        {(placeholder) => (
          <input className={"text-input" + (this.state.editing ? " text-input--editing" : "")}
                 onChange={this.onChange}
                 onBlur={this.onBlur}
                 onFocus={this.onFocus}
                 placeholder={placeholder}
                 ref={this.props.setRef || this.setRef}
            // onKeyDown={this.handleKeyDown}
                 value={this.state.editing ? this.state.value : this.props.value}/>
        )}
      </FormattedMessage>
    )
  }
}
