import React from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect
  // Link
} from "react-router-dom";

import {ROUTES} from "../routes";
import Home from "./home/home";
import Location from "./location/location";
import AdjustBillAmount from "./adjust/billAmount/adjustBillAmount";
import AdjustShading from "./adjust/shading/adjustShading";
import SolarPanels from "./adjust/solarPanels/solarPanels";
import Calculation from "./calculation/calculation";
import Login from "./login/login";
import Quote from "./quote/quote";

let _homePageVisited = false;

function App() {
  return (
    <Router>
      <Switch>
        <Route exact path={ROUTES.location} render={() => {
          _homePageVisited = true;
          return <Location />
        }}/>
        <Redirect exact from={ROUTES.adjust} to={ROUTES.adjustBillAmount} />
        <Route exact path={ROUTES.adjustBillAmount} render={() => (
          _homePageVisited ? <AdjustBillAmount /> : <Redirect to={ROUTES.home}/>
        )}/>
        <Route exact path={ROUTES.adjustShading} render={() => (
          _homePageVisited ? <AdjustShading /> : <Redirect to={ROUTES.home}/>
        )}/>
        <Route exact path={ROUTES.adjustSolarPanels} render={() => (
          // _homePageVisited ? <SolarPanels /> : <Redirect to={ROUTES.home}/>
          <SolarPanels />
        )}/>
        <Route exact path={ROUTES.calculation} render={() => (
          _homePageVisited ? <Calculation /> : <Redirect to={ROUTES.home}/>
        )}/>
        <Route exact path={ROUTES.login} render={() => (
          _homePageVisited ? <Login /> : <Redirect to={ROUTES.home}/>
        )}/>
        <Route exact path={ROUTES.quote} render={() => (
          _homePageVisited ? <Quote /> : <Redirect to={ROUTES.home}/>
        )}/>
        <Route exact path={ROUTES.home} render={() => {
          _homePageVisited = true;
          return <Home />
        }}/>
      </Switch>
        {/*<nav>*/}
        {/*  <ul>*/}
        {/*    <li>*/}
        {/*      <Link to="/">Home</Link>*/}
        {/*    </li>*/}
        {/*    <li>*/}
        {/*      <Link to="/about">About</Link>*/}
        {/*    </li>*/}
        {/*    <li>*/}
        {/*      <Link to="/users">Users</Link>*/}
        {/*    </li>*/}
        {/*  </ul>*/}
        {/*</nav>*/}
    </Router>
  );
}

export default App;
