import React from "react";
import { connect } from "react-redux";
import {getSolarPanels} from "../../../api/solarApi";
import {setSolarAcAnnual, setSolarCapacityFactor, setSolRadAnnual} from "../../../actions/solarFunctions";

class SolarPanelsGoogleMap extends React.Component {

  /*** google maps instanses ***/
  map = null;
  maxZoomService = null;
  solarListener = null;
  /*** elements ***/
  mapElement = null;

  myLatlng = {
    lat: 34.05207313615606,
    lng: -118.24387497622996
  };

  componentDidMount() {
    this.map = new window.google.maps.Map(this.mapElement, {
      zoom: 19,
      center: this.myLatlng,
      mapTypeId: 'satellite',
      tilt: 0,
      animatedZoom: false
    });
    this.maxZoomService = new window.google.maps.MaxZoomService();
    console.log("mapmap", this.map);
    console.log("maxZoomService", this.maxZoomService);
    if (this.props.googleMapsLocation) {
      this.setMapCenter(this.props);
    }
    this.solarListener = this.map.addListener('idle', this.getRoofMap);
  }

  getRoofMap = () => {
    getSolarPanels(this.props.googleMapsLocation || this.myLatlng, this.map)
      .then((response) => {
        console.log("getSolarPanels success:", response);
        // this.props.setSolRadAnnual(json.pvwatts.outputs.solrad_annual);
        // this.props.setSolarCapacityFactor(json.pvwatts.outputs.capacity_factor);
        //
        // let current_ac_annual = parseFloat(json.pvwatts.outputs.ac_annual);
        //
        // this.props.solarPanelsAmount.forEach((panel) => {
        //   panel.setMap(null);
        //   panel = null;
        // });
        // let panels = [];
        // console.log(json);
        //
        // json['rects'].forEach((rect) =>{
        //   let panel = new google.maps.Polygon({
        //     path: rect,
        //     geodesic: false,
        //     strokeColor: '#000000',
        //     strokeOpacity: 0.5,
        //     strokeWeight: 1
        //   });
        //   panel.setMap(map);
        //   panels.push(panel);
        // });
        // slider.max = panels.length;
        // slider.value = slider.max;
        // total_ac_annual = current_ac_annual * slider.value;
        // ac_annual.innerText = total_ac_annual;
        return response.data
      })
  };

  setMapCenter = (nextProps) => {
    this.map.setCenter(nextProps.googleMapsLocation);
    this.goMaxZoom(nextProps.googleMapsLocation);
  };

  goMaxZoom = (pos) => {
    this.maxZoomService.getMaxZoomAtLatLng(pos, (response) => {
      if (response.status === 'OK') {
        this.map.setZoom(20);
      }
    });
  };

  render() {
    return (
      <div className="solar-map" id="map" ref={(node) => this.mapElement = node} />
    )
  }
}

const mapStateToProps = (store) => {
  return {
    googleMapsLocation: store.locationState.googleMapsLocation,
    googleMapsPlace: store.locationState.googleMapsPlace,
    solarPanelsAmount: store.solarState.solarPanelsAmount,
  };
};

export default connect(mapStateToProps, {
  setSolRadAnnual,
  setSolarAcAnnual,
  setSolarCapacityFactor,
})(SolarPanelsGoogleMap);
