import * as types from '../actions/actionTypes';

const initialState = {
  solarPanelsAmountMin: 0,
  solarPanelsAmountMax: 100,
  solarPanelsAmount: 50,
  solarSolRadAnnual : 50,
  solarAcAnnual : 50,
  solarCapacityFactor: 50,
};

export default function (state = initialState, action) {

  switch (action.type) {

    case types.SET_SOLAR_PANELS_AMOUNT:
    case types.SET_SOLAR_PANELS_AMOUNT_MIN:
    case types.SET_SOLAR_PANELS_AMOUNT_MAX:
    case types.SET_SOLAR_SOLRAD_ANNUAL:
    case types.SET_SOLAR_AC_ANNUAL:
    case types.SET_SOLAR_CAPACITY_FACTOR:
      return Object.assign({}, state, {
        ...action.payload
      });
    default:
      return state
  }

}
