import React, {Fragment} from "react";
import { withRouter } from "react-router-dom";

import {ROUTES} from "../../../routes";
import {FormattedMessage} from "react-intl";

class SolarPanelsButton extends React.Component {

  handleButtonClick = () => {
    this.props.history.push(ROUTES.calculation)
  };

  render() {
    return (
      <Fragment>
        <button className="button-white button-white-text-green" onClick={this.handleButtonClick}>
          <FormattedMessage id="adjustSolar.buttonText" />
        </button>
      </Fragment>
    )
  }
}

export default withRouter(SolarPanelsButton);
