import React from 'react';
import axios from "axios";
import {IntlProvider} from 'react-intl';
// import en from "react-intl/locale-data/en";
import Papa from "papaparse";

// addLocaleData([...en]);

class TranslatesProvider extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      translations: null,
    };
  }

  componentDidMount() {
    axios.get("/translates/translates.csv")
      .then( (response) => {
        // handle success
        let translates = {};
        Papa.parse(response.data, {
          complete: (results) => {
            // console.log("Finished:", results.data);
            results.data[0].forEach((locale) => {
              if (locale !== "key") {
                translates[locale] = {};  // simple create an empty objects for each locale
              }
            });
            results.data.forEach((translatesArray, translatesArrayIndex) => { // vertical forEach
              if (translatesArrayIndex) { // not 0 (the first key line)
                const key = translatesArray[0]; // key, for ex. login.email
                translatesArray.forEach((translateValue, translateValueIndex) => { // horizontal forEach
                  if (translateValueIndex) { // skip first key
                    const locale = results.data[0][translateValueIndex]; // get first line and locale index
                    if (translateValue) {
                      translates[locale][key] = translateValue
                    }
                  }
                });
              }
            });
          }
        });
        this.setState({
          translations: translates,
        });
      })
      .catch(function (error) {
        // handle error
        console.log(error);
      })
  }

  render() {
    let locale = "en-US"; // temp. hardcore (change to document.kvConfig.translates when it is ready)
    return this.state.translations ? (
      <IntlProvider locale={locale} messages={this.state.translations[locale]}>
        {this.props.children}
      </IntlProvider>
    ) : null
  }
}

export default TranslatesProvider;
