import * as types from '../actions/actionTypes';

const initialState = {
  userInputLocation: "",
  googleMapsLocation: null,
  googleMapsPlace: null,
  markerPlaced: false
};

export default function (state = initialState, action) {

  switch (action.type) {

    case types.SET_USER_INPUT_LOCATION:
    case types.SET_GOOGLE_MAPS_MARKER:
      return Object.assign({}, state, {
        ...action.payload
      });
    case types.SET_GOOGLE_MAPS_LOCATION:
      return Object.assign({}, state, {
        googleMapsLocation: action.payload.googleMapsLocation,
        googleMapsPlace: action.payload.googleMapsPlace || state.googleMapsPlace
      });
    default:
      return state
  }

}
