import React from "react";
import { connect } from "react-redux";
import {setGoogleMapsLocation, setGoogleMapsMarker} from "../../actions/homeActions";

class LocationGoogleMap extends React.Component {

  /*** google maps instanses ***/
  map = null;
  marker = null;
  maxZoomService = null;
  /*** elements ***/
  mapElement = null;

  componentDidMount() {
    let myLatlng = {lat: 38.9443948, lng: -77.005108};
    this.map = new window.google.maps.Map(this.mapElement, {
      zoom: 19,
      center: myLatlng,
      mapTypeId: 'satellite',
      tilt: 0,
      animatedZoom: false
    });
    this.maxZoomService = new window.google.maps.MaxZoomService();
    this.setMapListeners();
    console.log("mapmap", this.map);
    console.log("maxZoomService", this.maxZoomService);
    if (this.props.googleMapsLocation) {
      this.setMapCenter(this.props);
    }
  }

  componentWillReceiveProps(nextProps) {
    if ((nextProps.googleMapsLocation && !this.props.googleMapsLocation) || (nextProps.googleMapsLocation && this.props.googleMapsLocation && (
      (nextProps.googleMapsLocation.lat !== this.props.googleMapsLocation.lat)
      || nextProps.googleMapsLocation.lng !== this.props.googleMapsLocation.lng
    ))) {
      this.setMapCenter(nextProps);
    }
  }

  setMapCenter = (nextProps) => {
    this.map.setCenter(nextProps.googleMapsLocation);
    this.goMaxZoom(nextProps.googleMapsLocation);
  };

  setMapListeners = () => {
    this.map.addListener('click', (e) => {
      const latLng = e.latLng;
      const lat = latLng.lat();
      const lng = latLng.lng();
      this.placeMarker(latLng);
      this.props.setGoogleMapsLocation({lat, lng});
    });
  };

  placeMarker = (pos) => {
    console.log("pos", pos);
    if (this.marker === null) {
      this.marker = new window.google.maps.Marker({
        position: pos,
        map: this.map
      });
    } else {
      this.marker.setPosition(pos);
    }
    this.props.setGoogleMapsMarker(true);
  };

  goMaxZoom = (pos) => {
    this.maxZoomService.getMaxZoomAtLatLng(pos, (response) => {
      if (response.status === 'OK') {
        this.map.setZoom(20);
      }
    });
  };

  render() {
    return (
      <div className="google-map__map" id="map" ref={(node) => this.mapElement = node} />
    )
  }
}

const mapStateToProps = (store) => {
  return {
    googleMapsLocation: store.locationState.googleMapsLocation,
    googleMapsPlace: store.locationState.googleMapsPlace,
  };
};

export default connect(mapStateToProps, {
  setGoogleMapsMarker,
  setGoogleMapsLocation
})(LocationGoogleMap);
