import React from "react";
import {connect} from "react-redux";
import {withRouter} from "react-router-dom";

import {ROUTES} from "../../../routes";
import {KEY_CODES} from "../../../constants";
import {FormattedMessage} from "react-intl";

class AdjustShadingButtons extends React.Component {

  componentDidMount() {
    document.addEventListener('keypress', this.keyDownPress);
  }

  componentWillUnmount() {
    document.removeEventListener('keypress', this.keyDownPress);
  }

  keyDownPress = (e) => {
    console.log("e", e);
    switch (e.keyCode) {
      case KEY_CODES.ENTER:
        return this.handleNextButtonClick();
      case KEY_CODES.ESCAPE:
        return this.handleBackButtonClick();
      default:
        break;
    }
  };

  handleNextButtonClick = () => {
    // this.props.history.push(ROUTES.adjustSolarPanels);
    this.props.history.push(ROUTES.adjustSolarPanels);
  };

  handleBackButtonClick = () => {
    this.props.history.push(ROUTES.adjustBillAmount);
  };

  render() {
    return (
      <div className="global-nav">
        <div className="global-nav__button">
          <FormattedMessage id="adjustShading.buttonBack" defaultMessage="Back">
            {(placeholder) => (
              <input type="button" className="button-green" value={placeholder} onClick={this.handleBackButtonClick} />
            )}
          </FormattedMessage>
        </div>
        <div className="global-nav__button">
          <FormattedMessage id="adjustShading.buttonNext" defaultMessage="Next">
            {(placeholder) => (
              <input type="button"
                     className="button-green"
                     disabled={!this.props.shadingType}
                     value={placeholder} onClick={this.handleNextButtonClick} />
            )}
          </FormattedMessage>
        </div>
      </div>
    )
  }
}

const mapStateToProps = (store) => {
  return {
    shadingType: store.adjustState.shadingType,
  };
};

export default connect(mapStateToProps, {

})(withRouter(AdjustShadingButtons));
