import { SolarAxiosInstance } from "./axiosInstance";

export const getSolarPanels = ({lat, lng}, map) => {

  let zoom = map.getZoom();
  let bounds = map.getBounds();
  let ne = bounds.getNorthEast();
  let sw = bounds.getSouthWest();
  let ne_s = ne.toString();
  let sw_s = sw.toString();

  let url = "/roof_map?";
  url += "lat=" + lat.toString() + "&";
  url += "lng=" + lng.toString() + "&";
  url += "ne=" + ne_s + "&";
  url += "sw=" + sw_s + "&";

  url += "zoom=" + zoom.toString();

  return SolarAxiosInstance.get(url)
    .then((response) => {
      console.log("getSolarPanels success:", response);
      return response ? response.data : {}
    })
};
