import React from "react";
import { CircularProgressbarWithChildren, buildStyles } from 'react-circular-progressbar';
import {FormattedMessage} from "react-intl";

import {connect} from "react-redux";
import RadialSeparators from "./radialSeparators";

class Co2 extends React.Component {

  render() {
    return (
      <div className="co2">
        <div className="co2-circle">
          <CircularProgressbarWithChildren
            value={66}
            className={"co2-circle__svg"}
            strokeWidth={10}
            styles={buildStyles({
              pathColor: "#ffffff",
              trailColor: "rgba(255,255,255,0.4)",
              strokeLinecap: "butt"
            })} >
            <RadialSeparators
              count={18}
              style={{
                background: "#95BA1B",
                width: "3px",
                // This needs to be equal to props.strokeWidth
                height: `${10}%`
              }}
            />
            <h2 className="co2-amount">{`${66}%`}</h2>
          </CircularProgressbarWithChildren>
        </div>
        <p className="text-center"><FormattedMessage id="adjustSolar.circleCO2"/></p>
      </div>
    )
  }
}

const mapStateToProps = (store) => {
  return {
    solarPanelsAmountMin: store.solarState.solarPanelsAmountMin,
    solarPanelsAmountMax: store.solarState.solarPanelsAmountMax,
    solarPanelsAmount: store.solarState.solarPanelsAmount,
  };
};

export default connect(mapStateToProps, {

})(Co2);
