import React from "react";
import { connect } from "react-redux";
import LocationContinueButton from "./locationContinueButton";
import LocationGoogleMap from "./locationGoogleMap";

class Location extends React.Component {

  componentDidMount() {
    document.body.classList.add("location");
  }

  componentWillUnmount() {
    document.body.classList.remove("location");
  }

  render() {
    return (
      <div className={"google-map" + (this.props.markerPlaced ? " google-map--with-marker" : "")}>
        <LocationGoogleMap/>
        <LocationContinueButton/>
      </div>
    )
  }
}

const mapStateToProps = (store) => {
  return {
    markerPlaced: store.locationState.markerPlaced,
  };
};

export default connect(mapStateToProps, {

})(Location);
