import React from "react";
import { connect } from "react-redux";
import {FormattedMessage} from "react-intl";

import {setCalculations} from "../../actions/calculationActions";
import * as types from '../../actions/actionTypes';

import LogoQuote from "../../svg/logoQuote";
import SolarOutputSection from "./sections/solarOutputSection";
import PanelCostsSection from "./sections/panelCostsSection";
import MonthlyBillSection from "./sections/monthlyBillSection";
import CalculationButton from "./calculationButton";

class Calculation extends React.Component {

  componentDidMount() {
    document.body.classList.add("calculation");
    setTimeout(() => {
      this.props.setCalculations(types.SET_SOLAR_OUTPUT_CALCULATED, "solarOutputCalculated", true);
    }, 1000);
    setTimeout(() => {
      this.props.setCalculations(types.SET_PANEL_COSTS_CALCULATED, "panelCostsCalculated", true);
    }, 2000);
    setTimeout(() => {
      this.props.setCalculations(types.SET_MOTHLY_BILL_CALCULATED, "monthlyBillCalculated", true);
    }, 3000);
    setTimeout(() => {
      this.props.setCalculations(types.SET_EVERYTHING_CALCULATED, "everythingCalculated", true);
    }, 3500);
  }

  componentWillUnmount() {
    document.body.classList.remove("calculation");
  }

  render() {
    return (
      <div className="container">
        <LogoQuote/>
        <section id="panel-overlay">
          <SolarOutputSection solarOutputCalculated={this.props.solarOutputCalculated}
                              everythingCalculated={this.props.everythingCalculated}/>
          <PanelCostsSection panelCostsCalculated={this.props.panelCostsCalculated}
                             everythingCalculated={this.props.everythingCalculated}/>
          <MonthlyBillSection monthlyBillCalculated={this.props.monthlyBillCalculated}
                              everythingCalculated={this.props.everythingCalculated}/>
          {this.props.everythingCalculated
            ? <CalculationButton/>
            : <div id="calculation-text"><FormattedMessage id="calculation.calculatingText" /></div>
          }
        </section>
      </div>
    )
  }
}

const mapStateToProps = (store) => {
  return {
    solarOutputCalculated: store.calculationState.solarOutputCalculated,
    panelCostsCalculated: store.calculationState.panelCostsCalculated,
    monthlyBillCalculated: store.calculationState.monthlyBillCalculated,
    everythingCalculated: store.calculationState.everythingCalculated,
  };
};

export default connect(mapStateToProps, {
  setCalculations
})(Calculation);
