import React from "react";
import {ROUTES} from "../../../routes";
import {KEY_CODES} from "../../../constants";
import {withRouter} from "react-router-dom";
import {FormattedMessage} from "react-intl";

class AdjustBillAmountButtons extends React.Component {

  componentDidMount() {
    document.addEventListener('keypress', this.keyDownPress);
  }

  componentWillUnmount() {
    document.removeEventListener('keypress', this.keyDownPress);
  }

  keyDownPress = (e) => {
    console.log("e", e);
    switch (e.keyCode) {
      case KEY_CODES.ENTER:
        return this.handleNextButtonClick();
      case KEY_CODES.ESCAPE:
        return this.handleBackButtonClick();
      default:
        break;
    }
  };

  handleNextButtonClick = () => {
    this.props.history.push(ROUTES.adjustShading);
  };

  handleBackButtonClick = () => {
    this.props.history.push(ROUTES.location);
  };

  render() {
    return (
      <div className="global-nav">
        <div className="global-nav__button">
          <FormattedMessage id="adjustBillAmount.buttonBack" defaultMessage="Back">
            {(placeholder) => (
              <input type="button" className="button-green" value={placeholder} onClick={this.handleBackButtonClick} />
            )}
          </FormattedMessage>
        </div>
        <div className="global-nav__button">
          <FormattedMessage id="adjustBillAmount.buttonNext" defaultMessage="Next">
            {(placeholder) => (
              <input type="button" className="button-green" value={placeholder} onClick={this.handleNextButtonClick} />
            )}
          </FormattedMessage>
        </div>
      </div>
    )
  }
}

export default withRouter(AdjustBillAmountButtons)
