import * as types from '../actions/actionTypes';

const initialState = {
  fullName: "",
  phone: "",
  email: "",
  zip: ""
};

export default function (state = initialState, action) {

  switch (action.type) {

    case types.SET_USER_INPUT_VALUE:
      return Object.assign({}, state, {
        [action.payload.property]: action.payload.value
      });
    default:
      return state
  }

}
