import React from "react";
import {connect} from "react-redux";
import {FormattedMessage} from "react-intl";
import {buildStyles, CircularProgressbarWithChildren} from 'react-circular-progressbar';
// import 'react-circular-progressbar/dist/styles.css';

class Savings extends React.Component {

  resizeId;

  componentDidMount() {
    this.setRingDimensions();
    window.addEventListener("resize", this.setRingDimensions);
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.setRingDimensions);
  }

  setRingDimensions = () => {
    const leftSide = document.querySelector(".solar-panel__circles-left");
    const savingsCircle = document.querySelector(".savings-circle");
    if (leftSide && savingsCircle) {
      if (leftSide.clientHeight < leftSide.clientWidth) {
        savingsCircle.style.maxHeight = leftSide.clientHeight - 30;
        savingsCircle.style.maxWidth = leftSide.clientHeight - 30;
      } else {
        savingsCircle.style.maxHeight = leftSide.clientWidth - 30;
        savingsCircle.style.maxWidth = leftSide.clientWidth - 30;
      }
    }
  };

  handleResize = () => {
    clearTimeout(this.resizeId);
    this.resizeId = setTimeout(this.setRingDimensions, 100);
  };

  render() {
    const styles = {};
    if (this.props.maxWidth) {
      styles.maxWidth = this.props.maxWidth
    }
    return (
      <div className="savings" style={styles}>
        <CircularProgressbarWithChildren value={66}
                                         className={"savings-circle"}
                                         styles={buildStyles({
                                           pathColor: "#ffffff",
                                           trailColor: "rgba(255,255,255,0.2)"
                                         })}>
          <h1 className="savings-amount">$1000</h1>
          <h4 className="savings-text"><FormattedMessage id="adjustSolar.circleSavings"/></h4>
        </CircularProgressbarWithChildren>

      </div>
    )
  }
}

const mapStateToProps = (store) => {
  return {
    solarPanelsAmountMin: store.solarState.solarPanelsAmountMin,
    solarPanelsAmountMax: store.solarState.solarPanelsAmountMax,
    solarPanelsAmount: store.solarState.solarPanelsAmount,
  };
};

export default connect(mapStateToProps, {

})(Savings);
