import * as types from './actionTypes';

export const setSolarPanelsAmount = (solarPanelsAmount) => ({
  type: types.SET_SOLAR_PANELS_AMOUNT,
  payload: { solarPanelsAmount }
});

export const setSolarPanelsAmountMin = (solarPanelsAmountMin) => ({
  type: types.SET_SOLAR_PANELS_AMOUNT_MIN,
  payload: { solarPanelsAmountMin }
});

export const setSolarPanelsAmountMax = (solarPanelsAmountMax) => ({
  type: types.SET_SOLAR_PANELS_AMOUNT_MAX,
  payload: { solarPanelsAmountMax }
});

export const setSolRadAnnual = (solarSolRadAnnual) => ({
  type: types.SET_SOLAR_SOLRAD_ANNUAL,
  payload: { solarSolRadAnnual }
});

export const setSolarAcAnnual = (solarAcAnnual) => ({
  type: types.SET_SOLAR_AC_ANNUAL,
  payload: { solarAcAnnual }
});

export const setSolarCapacityFactor = (solarCapacityFactor) => ({
  type: types.SET_SOLAR_CAPACITY_FACTOR,
  payload: { solarCapacityFactor }
});
