import React from "react";
import { connect } from "react-redux";
import {FormattedMessage} from "react-intl";

import HomeStartQuoteButton from "./homeStartQuoteButton";
import LogoQuote from "../../svg/logoQuote";
import UiInput from "../ui/input";
import {setUserInputLocation, setGoogleMapsLocation} from "../../actions/homeActions";

class Home extends React.Component {

  inputElement = null;
  autocomplete = null;

  componentDidMount() {
    this.autocomplete = new window.google.maps.places.Autocomplete(this.inputElement);
    this.autocomplete.addListener('place_changed', () => {
      let place = this.autocomplete.getPlace();
      let location = place.geometry.location;
      this.props.setGoogleMapsLocation(location, place);
      if (this.inputElement) {
        this.props.setUserInputLocation(this.inputElement.value);
      }
    });
  }

  render() {
    return (
      <div className="container">
        <LogoQuote style={{marginBottom: "100px"}}/>
        <p style={{margin: "0 0 60px 0"}} className="text-center">
          <FormattedMessage id="home.enterLocation"/>
        </p>
        <UiInput value={this.props.userInputLocation}
                 setRef={node => this.inputElement = node}
                 placeholder={"home.inputPlaceholder"}
                 onChange={this.props.setUserInputLocation}/>
        <HomeStartQuoteButton/>
      </div>
    )
  }
}

const mapStateToProps = (store) => {
  return {
    userInputLocation: store.locationState.userInputLocation,
  };
};

export default connect(mapStateToProps, {
  setUserInputLocation,
  setGoogleMapsLocation
})(Home);
