import React, {Fragment} from "react";
import { withRouter } from "react-router-dom";

import {ROUTES} from "../../routes";
import {KEY_CODES} from "../../constants";
import {FormattedMessage} from "react-intl";

class CalculationButton extends React.Component {

  componentDidMount() {
    document.addEventListener('keypress', this.keyDownPress);
  }

  componentWillUnmount() {
    document.removeEventListener('keypress', this.keyDownPress);
  }

  keyDownPress = (e) => {
    if (e.keyCode === KEY_CODES.ENTER) {
      this.handleButtonClick();
    }
  };

  handleButtonClick = () => {
    this.props.history.push(ROUTES.login)
  };

  render() {
    return (
      <Fragment>
        <div id="calculation-text">
          <FormattedMessage id="calculation.finishedText" />
        </div>
        <button className="button-white button-white-text-green" onClick={this.handleButtonClick}>
          <FormattedMessage id="calculation.unlockButton" />
        </button>
      </Fragment>
    )
  }
}

export default withRouter(CalculationButton);
