import React from "react";
import { connect } from "react-redux";
import ReactSlider from 'react-slider'

import LogoQuote from "../../../svg/logoQuote";
import AdjustBillAmountButtons from "./adjustBillAmountButtons";
import {FormattedMessage} from "react-intl";
import {setElectricBillAmount} from "../../../actions/adjustActions";

class AdjustBillAmount extends React.Component {

  state = {
    value: this.props.value,
    editing: false
  };

  componentDidMount() {
    this.setState({
      value: this.props.electricBillAmount
    })
  }

  onChange = (value) => {
    this.setState({
      value
    })
  };

  onAfterChange = (event) => {
    this.setState({
      editing: false
    }, () => {
      this.props.setElectricBillAmount(this.state.value);
    })
  };

  onBeforeChange = () => {
    this.setState({
      editing: true,
      value: this.props.electricBillAmount
    })
  };

  render() {
    return (
      <div className="container">
        <LogoQuote style={{marginBottom: "100px"}}/>
        <h1 style={{marginBottom: "40px"}}>${this.state.value}</h1>
        <ReactSlider
          min={this.props.electricBillMin}
          max={this.props.electricBillMax}
          onChange={this.onChange}
          onBeforeChange={this.onBeforeChange}
          onAfterChange={this.onAfterChange}
          value={this.state.editing ? this.state.value : this.props.electricBillAmount}
          className="slider-wrapper"
          thumbClassName="slider-button"
          trackClassName="slider-range"
          renderThumb={(props, state) => <div {...props}>{state.valueNow}</div>}
        />
        <p style={{marginBottom: "40px"}}>
          <FormattedMessage id="adjustBillAmount.text"/>
        </p>
        <AdjustBillAmountButtons/>
      </div>
    )
  }
}

const mapStateToProps = (store) => {
  return {
    electricBillMin: store.adjustState.electricBillMin,
    electricBillMax: store.adjustState.electricBillMax,
    electricBillAmount: store.adjustState.electricBillAmount,
  };
};

export default connect(mapStateToProps, {
  setElectricBillAmount
})(AdjustBillAmount);
