import React from "react";
import { withRouter } from "react-router-dom";
import {ROUTES} from "../../routes";
import {KEY_CODES} from "../../constants";
import {FormattedMessage} from "react-intl";

class LoginButton extends React.Component {

  componentDidMount() {
    document.addEventListener('keypress', this.keyDownPress);
  }

  componentWillUnmount() {
    document.removeEventListener('keypress', this.keyDownPress);
  }

  keyDownPress = (e) => {
    if (e.keyCode === KEY_CODES.ENTER) {
      this.handleButtonClick();
    }
  };

  handleButtonClick = () => {
    this.props.handleButtonClick();
    setTimeout(() => {
      this.props.history.push(ROUTES.quote)
    }, 1000)
  };

  render() {
    return (
      <div className="global-nav">
        <FormattedMessage id="login.buttonGetQuote" defaultMessage="Get My Free Login">
          {(placeholder) => (
            <input type="button"
                   onClick={this.handleButtonClick}
                   className="button-green"
                   value={placeholder} />
          )}
        </FormattedMessage>

      </div>
    )
  }
}

export default withRouter(LoginButton)

