import React from "react";
import { withRouter } from "react-router-dom";
import {ROUTES} from "../../routes";
import {FormattedMessage} from "react-intl";
import {connect} from "react-redux";
import {setGoogleMapsMarker} from "../../actions/homeActions";

class LocationContinueButton extends React.Component {

  handleButtonClick = () => {
    this.props.history.push(ROUTES.adjustBillAmount)
  };

  render() {
    return (
      <div className="google-map__bottom">
        <div className="container text-center">
          <p><FormattedMessage id="location.bottomText" defaultMessage="Place marker"/></p>
          {this.props.markerPlaced ? (
            <button className="button-white" onClick={this.handleButtonClick}>
              <FormattedMessage id="location.continueButton" defaultMessage="Continue"/>
            </button>
          ) : null}
        </div>
      </div>
    )
  }
}

const mapStateToProps = (store) => {
  return {
    markerPlaced: store.locationState.markerPlaced,
  };
};

export default connect(mapStateToProps, {
  setGoogleMapsMarker
})(withRouter(LocationContinueButton));

